    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">2.&nbsp;为实现向您提供我们服务的附加功能，您可选择授权我们收集、使用的信息。如您拒绝提供，您将无法正常使用相关附加功能或无法达到我们拟达到的功能效果，但并不会影响您正常使用我们服务的基本功能。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">我们可能向您收集的个人信息，包括通过使用AI智能问询平台（含下述任何方式）您向我们提供的【您的名字、姓氏、称谓和/或电子邮箱、联系电话号码或其他联系方式、您的声纹或面部信息、您拟使用的会员名和密码、您拟用于储存AI智能问询的数字钱包地址、银行卡号】等个人基本信息。我们也可能在您使用本AI智能问询平台的过程中，收集某些您的非个人身份识别资讯，例如您使用的互联网浏览器和操作系统规格、网站浏览点击数据等。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">本政策适用于通过AI智能问询平台收到（无论是自动地或通过用户直接输入的或其他方式收到）的全部个人信息。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">我们会为您提供的各项具体功能场景包括：</span></p>
