    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">（二）&nbsp;向您提供、匹配更契合您需求的产品</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">如果您希望本AI智能问询平台向您提供/匹配更契合您需求的产品信息，我们会收集和使用您在访问或使用本AI智能问询平台网站或客户端时的浏览、搜索记录及设备信息、服务日志信息，提取您的偏好特征，并向您推送您可能感兴趣的产品。包括：</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">1.&nbsp;网络日志</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">我们使用标准网络日志，记录访问我们网站用户的基本数据。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">该等日志包含如下信息：</span></p>
