    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">2. Cookies和其他技术</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">为提升您的使用体验，本AI智能问询平台的网页可能采用cookie或相关技术。Cookie是一些设置于您的互联网浏览器、用于储存您个人喜好的文字档案，并不会对外透露您的电邮地址或其他个人身份识别资讯。我们也有可能会收集您使用本AI智能问询平台时所访问过的网页、点击过的链接或广告及进行过的其他操作的相关信息。在您未拒绝接受cookies的情况下，cookies将被发送到您的浏览器，并储存在您的计算机硬盘。您一旦选择于本AI智能问询平台输入您的个人身份识别资讯，该等信息可能会被连结到储存于cookie的数据。我们使用cookies储存您访问我们AI智能问询平台的相关数据，以便在您访问或再次访问我们的AI智能问询平台时,我们能识别您的身份，并通过分析数据为您提供更好更多的服务。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">您有权选择接受或拒绝接受cookies。您可以通过修改浏览器的设置以拒绝接受cookies，但是我们需要提醒您，因为您拒绝接受cookies，您可能无法使用依赖于cookies的我们AI智能问询平台的部分功能。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">3.&nbsp;设备信息</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">当您使用我们的服务或访问本AI智能问询平台时，我们也收集您的移动设备发送的某些信息，如设备标识符、用户设置和您设备的操作系统。我们所收集的有关您的移动设备和互联网连接的信息可能包括：唯一设备标识符、IMEI号码、IP地址、操作系统、浏览器类型、浏览器语言、引用/退出页面和URLs、移动网络信息和该设备的电话号码。我们能让您下载工具、应用程序和小程序，以使您能够在任何移动设备上使用服务，该工具、应用程序和小程序会在您的移动设备或其他设备上存储信息。该等工具会向我们传输个人信息，以使您能够访问您的用户帐户，并且让我们能够追踪这些工具的使用情况。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">二、&nbsp;我们如何使用您的信息</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">我们获取、持有、使用和处理您的个人信息，以提供、保持和管理您的登录账号，基于您的个人信息与您的明示同意且出于本政策列出的合法商业目的，为您提供/匹配产品。此外，出于安全目的需要将不时检查服务器的登录—例如，检测有关服务的未经授权活动。该等情况下，服务器登录数据包括IP地址可能会提供给执法机构，以便他们识别与他们调查的未经授权活动有关的用户。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">（一）&nbsp;出于我们管理目的使用</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">我们也可以为我们的管理目的使用您的个人信息，例如考量我们的AI智能问询平台服务的利益和/或开发我们的AI智能问询平台服务、确保内部质量控制、遵守法规要求（包括防止潜在的被禁止的或非法的活动），以及执行我们的使用条款。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">元AI</span><span style="font-size: 10.5pt;">可出于我们享有合法利益的其它目的使用个人信息，例如为将来任何的产品匹配与您联系、个人或集市研究、反欺诈保护或在您提供个人信息或您作出同意时向您披露的任何其它目的。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">（二）&nbsp;组合数据的使用以及去标识化或匿名化信息</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">我们组合和使用来自我们网络日志和其他技术的非个人信息（组合数据）来研究本AI智能问询平台是如何被使用的。我们会定期地分析组合数据并使用我们学习到的内容来改进我们的AI智能问询平台。此外，我们可以与我们的合作伙伴分享组合数据（不包含任何可以识别某一个人的信息）来帮助他们更好地设计他们的AI智能问询平台。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">元AI</span><span style="font-size: 10.5pt;">可使用您的个人信息和其他关于您的信息来创造匿名和/或组合信息，例如去标识化的个人信息、去标识化的位置信息、有关您访问本AI智能问询平台服务所用计算机或设备的信息、本网站或我们创建的关于我们产品和服务的分析。匿名化的和/或组合信息将用于多种功能，包括用于评测访客对产品的不同部分或功能的兴趣和使用。匿名化的和组合的信息不是个人信息，并且我们可以通过多种方式，包括研究、内部分析、解析以及其他法律允许的任何目的，使用该等信息。为</span><span style="font-size: 10.5pt;">元AI</span><span style="font-size: 10.5pt;">及第三方之目的，我们可以以匿名化的或组合的形式（设计用于防止任何人识别出您的身份）在</span><span style="font-size: 10.5pt;">元AI</span><span style="font-size: 10.5pt;">内部及与第三方共享这些信息。我们可以以适用法律允许的任何方式使用和披露这些匿名的、去标识化的和/或组合的信息，此类处理后信息的使用无需另行向您通知并征得您的同意。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">三、&nbsp;收集和使用个人信息的其他规则</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">1.&nbsp;若你提供的信息中含有其他用户的个人信息，在向我们的AI智能问询平台提供这些个人信息之前，您需确保您已经取得合法的授权。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">2.&nbsp;若我们将个人信息用于本政策未载明的其他用途，或者将基于特定目的收集而来的个人信息用于其他目的，均会事先获得您的同意。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">3.&nbsp;若我们从第三方处间接获取您的个人信息的，我们会在收集前明确以书面形式要求该第三方说明其个人信息来源，以及是否已经就其收集、处理以及向我们提供您的个人信息取得了您的合法授权。除法律明确允许的情形外，我们只会在确认第三方已经取得您的授权同意后再从第三方收集您的个人信息，如果第三方的授权范围无法涵盖我们的处理和使用目的时，我们会自行或者要求该第三方征得您的同意后再行处理您的个人信息。同时，我们的专业安全团队对个人信息会进行安全加固（包括敏感信息报备、敏感信息加密存储、访问权限控制等）。我们会使用不低于我们对自身用户个人信息同等的保护手段与措施对间接获取的个人信息进行保护。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">4.&nbsp;征得授权同意的例外</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">根据相关法律法规规定，我们在以下情况下收集、使用您的个人信息无需您的授权同意：</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;与我们履行法律法规规定的义务相关的</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;与国家安全、国防安全直接相关的</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;与公共安全、公共卫生、重大公共利益直接相关的</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;与犯罪侦查、起诉、审判和判决执行等司法或行政执法直接相关的</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;出于维护您或其他个人的生命、财产等重大合法权益但又很难得到本人同意的</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;您自行向社会公众公开的个人信息</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;从合法公开披露的信息中收集的个人信息，如合法的新闻报道、政府信息公开等渠道</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;与您签订和履行相关协议或其他书面文件所必需的</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;用于维护所提供的产品及/或服务的安全稳定运行所必需的，例如发现、处置产品及/或服务的故障</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;法律法规规定的其他情形</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">5.&nbsp;如我们停止运营AI智能问询平台服务，我们将及时停止继续收集您个人信息的活动，将停止运营的通知以逐一送达或公告的形式通知您，并对我们所持有的与已关停业务相关的个人信息进行删除或匿名化处理。涉及儿童个人信息的，我们会并将停止运营的通知及时告知儿童监护人。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">四、&nbsp;我们会如何共享您的个人信息</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">我们不会与</span><span style="font-size: 10.5pt;">元AI</span><span style="font-size: 10.5pt;">以外的公司、组织和个人共享您的个人信息，但是以下情况除外：</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">（一）&nbsp;供应商和服务提供商</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">我们可能委托授权合作伙伴亦即供应商和服务提供商为您提供某些服务或代表我们履行职能，这种情况下，我们会仅出于本政策声明的合法、正当、必要、特定、明确的目的共享您的个人信息，以支持您参与或潜在参与</span><span style="font-size: 10.5pt;">元AI</span><span style="font-size: 10.5pt;">活动，并允许我们提供其他活动相关的服务。我们交托个人信息的服务提供商（处理者）的类型包括以下服务提供商：（i）提供IT和相关服务；（ii）提供您要求的产品和服务；（iii）付款处理；以及（iv）客户服务活动；以及（v）与提供AI智能问询电商有关的其他活动。</span><span style="font-size: 10.5pt;">元AI</span><span style="font-size: 10.5pt;">已经与服务提供商签署了适当的合同，禁止服务提供商使用或共享个人信息，除非是代表我们履行合同约定服务所必要的或为遵守所适用的法律要求所必需的。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">（二）&nbsp;法定情形下与第三方共享信息</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">我们可能需要与第三方共享有关您的个人信息，以响应执法或国家安全请求或法律程序，如搜查令、传票或法院命令，或与涉嫌或实际非法活动有关的调查或起诉。披露也可能是为了遵守法律、保护我们或您的合法权利，或他人的合法权利，包括财产或安全，保护我们AI智能问询平台的安全性或完整性，采取预防措施防范责任，或在人身安全可能受到威胁的紧急情况下，或如果我们善意地相信披露在其他方面是必要的或明智的。虽然这些类型的披露不太可能发生，但它们仍然是可能的，因此我们在这里对他们进行了提示说明。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">五、&nbsp;我们会如何转让您的个人信息</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">我们不会将您的个人信息转让给任何公司、组织和个人，但以下情况除外：</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;在获取明确同意的情况下转让：获得您的明确同意后，我们会向其他方转让您的个人信息</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;在AI智能问询平台服务提供者</span><span style="font-size: 10.5pt;">元AI</span><span style="font-size: 10.5pt;">发生合并、收购或破产清算情形，或其他涉及合并、收购或破产清算情形时，如涉及到个人信息转让，我们会要求新的持有您个人信息的公司、组织继续受本政策的约束，否则我们将要求该公司、组织和个人重新向您征求授权同意</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">六、&nbsp;我们会如何披露您的个人信息</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">我们仅会在以下情况下，公开披露您的个人信息：</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;获得您明确同意或基于您的主动选择，我们可能会公开披露您的个人信息</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;如果我们确定您出现违反法律法规或严重违反AI智能问询平台相关协议及规则的情况，或为保护AI智能问询平台用户或公众的人身财产安全免遭侵害，我们可能依据法律法规或征得您同意的情况下披露关于您的个人信息，包括相关违规行为以及AI智能问询平台已对您采取的措施</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">七、&nbsp;共享、转让、公开披露个人信息时事先征得授权同意的例外</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">根据相关法律法规规定，以下情形中，共享、转让、公开披露您的个人信息无需事先征得您的授权同意：</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;与我们履行法律法规规定的义务相关的</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;与国家安全、国防安全直接相关的</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;与公共安全、公共卫生、重大公共利益直接相关的</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;与犯罪侦查、起诉、审判和判决执行等司法或行政执法直接相关的</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;出于维护您或其他个人的生命、财产等重大合法权益但又很难得到本人同意的；</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;您自行向社会公众公开的个人信息；</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;从合法公开披露的信息中收集个人信息的，如合法的新闻报道、政府信息公开等渠道</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">请知悉，根据适用的法律，若我们对个人信息采取技术措施和其他必要措施进行处理，使得数据接收方无法重新识别特定个人且不能复原，则此类处理后数据的共享、转让、公开披露无需另行向您通知并征得您的同意。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">八、&nbsp;我们会如何保护您的个人信息</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">保护作为用户的您对于我们的业务至关重要。维护一个安全的网络和服务器系统符合我们的用户和我们的伙伴的最大利益。我们使用具备行业标准的物理、技术和管理保护措施来处理和存储个人信息，以防止该等个人信息遭受可预见的风险，例如未经授权的使用、访问、披露、销毁和修改。该等安全保障措施包括使用加密、密码保护和用户认证访问权限。虽然我们将尽合理努力创建一个安定、安全和可靠的AI智能问询平台，但无法保证通过互联网向本AI智能问询平台或从本AI智能问询平台传输的任何通信或材料的机密性。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">我们建立了以数据为核心、围绕数据生命周期进行的数据安全管理体系，从组织建设、制度设计、人员管理、产品技术等方面多维度提升个人信息的安全性。我们会举办安全和隐私保护培训课程，加强员工对于保护个人信息重要性的认识。我们将不定期更新并公开安全风险、个人信息安全影响评估报告等有关内容，您可通过AI智能问询平台公告方式获得。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">如果我们的物理、技术或管理防护设施遭到破坏，导致信息被非授权访问、公开披露、篡改或毁坏，导致您的合法权益受损的，我们会及时启动应急预案，采取合理救济措施，以尽可能降低对您个人信息和其他权益的影响。在不幸发生个人信息安全事件后，我们还将按照法律法规的要求向您告知：安全事件的基本情况和可能的影响、我们已采取或将要采取的处置措施、您可自主防范和降低风险的建议、对您的补救措施等。事件相关情况我们将以邮件、信函、电话、推送通知等方式告知您，难以逐一告知个人信息主体时，我们会采取合理、有效的方式发布公告。同时，我们还将按照监管部门要求，上报个人信息安全事件的处置情况。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">您个人信息的安全也是您责任的一部分。当您通过本AI智能问询平台注册时，您应该选择难以猜测的密码，并且您必须对您的密码保密。您不应与任何人分享您的密码，也不应以不受保护的格式存储它，例如将其输入到您的计算机或保存在您的电子邮件中。我们不承担非故意披露的责任。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">九、&nbsp;我们会如何储存您的个人信息</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">您提供给我们的信息可能会由我们根据备份流程定期存档或存储，并且保留的时间为该等信息收集时的目的、向您提供我们的产品和服务、解决争议、建立法律抗辩、进行审计，追求合法的商业目的、执行我们的协议并遵守适用的法律所要求的时间。例如，《中华人民共和国电子商务法》要求商品和服务信息、交易信息保存时间自交易完成之日起不少于三年。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">我们判断个人信息的存储期限主要依据以下标准：</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">1、完成与您相关的交易目的、维护相应交易及业务记录，以应对您可能的查询或投诉；</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">2、保证我们为您提供服务的安全和质量；</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">3、您是否同意更长的留存期间；</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">4、是否存在关于保留期限的其他特别约定或法律法规规定。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">在超出保留期间后，我们会根据适用法律的要求删除或匿名化处理您的个人信息。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">十、&nbsp;访问、更正、删除和清除个人信息</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">您可以通过向</span><span style="font-size: 10.5pt;">元AI公众号</span><span style="font-size: 10.5pt;">发送电子邮件来访问、更新、更正或删除您提供给我们的个人信息。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">您可以通过向</span><span style="font-size: 10.5pt;">元AI公众号</span><span style="font-size: 10.5pt;">发送电子邮件，要求我们向您提供我们保存的有关您的个人信息和副本。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">如果您对您的个人信息有任何要求或对隐私有任何疑问或意见，请发送电子邮件至</span><span style="font-size: 10.5pt;">元AI公众号</span><span style="font-size: 10.5pt;">。请提供您的联系信息、本AI智能问询平台或所涉服务的名称，以及对您的请求或隐私顾虑的详细说明。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">请注意：</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">尽管我们善意地为用户提供访问其个人信息的权限，但在某些情况下，我们可能无法提供访问或删除某些信息，包括但不限于，含有法律特权信息的信息、根据法律必须保留保留的信息、会损害他人的隐私权或其他合法权利的信息、提供访问权的负担或费用与个人隐私权的风险不成比例的信息、在商业上是专有的信息。如果我们确定在任何特定情况下应限制访问，我们将向您解释为何会做出此决定，并提供联系点供您任何进一步问询。为保护您的隐私，我们将采取商业上合理的步骤，在允许访问或更改您的个人信息之前验证您的身份。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">我们数据库中不可识别的信息不会被删除。与所有负责任的AI智能问询平台一样，我们保留备份文件以防自然灾害、设备故障或其他中断。备份文件保护了您和我们，因为它们降低了丢失有价值数据的风险。从主文件中删除一条记录并不会从任何备份系统中删除该记录。通过正常的备份文件循环更新，备份记录中的所有信息可能需要6个月才能完全消除。同时，只要备份记录存在，它们就会获得与我们其它记录相同的隐私和安全保护。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">本政策项下从您处收到的所有请求将按照所适用的法律进行处理。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">（一）&nbsp;您的选择</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;您始终可以选择不提供信息。但是，如果您选择不提供信息，您将无法使用任何需要您登录的服务。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;即使您已经同意我们处理您的个人信息，您仍可以随时撤回该同意，并按照本政策中的说明选择退出并进一步处理。这不会对之前已经收集、使用或披露的信息产生影响。即使您选择退出，我们仍可收集和使用有关您在我们的网站上活动的非个人信息。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;您可以改变您授权我们收集和处理您的个人信息的范围。但请注意，对于部分类型的个人信息，例如实现AI智能问询平台基本功能所必须的信息或者我们履行法律法规规定的义务所必需的信息，我们可能没有办法响应您改变授权范围的请求。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;您可以注销您的登录账户。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;重要提示：如果您希望更正、修改或删除由第三方（而非您直接）提供给我们的个人信息，您可以联系我们访问、更正或从我们的文件中删除您的信息。但是，由于您依据第三方网络运营者或其他实体的隐私政策向他们提供了该等信息，我们还建议您与第三方网络运营者或其他实体联系，因为我们不控制或不对那些实践和隐私政策负责。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">（二）&nbsp;请勿追踪</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">请勿追踪（“请勿追踪”）是用户可以在某些网络浏览器中设置的隐私偏好。请勿追踪是一种让用户通知网站和服务，他们不希望通过网站或在线服务收集有关其网页访问的特定信息的方式。请注意，我们不回应或履行由网络浏览器传输的请勿追踪信号或类似的机制。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">（三）&nbsp;响应您的上述请求</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">为保障您的账户安全，您向我们提出上述请求时，我们可能会先要求您验证自己的身份（如要求您提供书面请求或其他方式），然后再处理您的请求。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">对于您向我们提出的上述请求，我们将在合理时间内做出答复。如您不满意我们的答复，还可以直接向我们的个人信息保护专职部门发起投诉。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">对于您合理的请求，我们原则上不收取费用，但对多次重复、超出合理限度的请求，我们将酌情收取一定费用。对于与您的身份不直接关联的信息、无端重复信息，或者需要过多技术手段（例如，需要开发新系统或从根本上改变现行惯例）、给他人合法权益带来风险或者不切实际的请求，我们可能会予以拒绝。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">十一、国际传输</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">我们通过直接用户输入收到的任何个人信息都存储在我们位于中国的服务器上。通过选择访问本AI智能问询平台、使用服务或以其他方式向我们提供信息，您同意将此存储在位于中国的服务器上。</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">在符合以下情形之一的前提下，我们会在取得您的单独同意并履行了法律规定的义务后，向境外实体提供您的个人信息：</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;依法律要求通过国家网信部门组织的安全评估；</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;按照国家网信部门的规定经专业机构进行个人信息保护认证；</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;与境外接收方订立合同，约定双方的权利和义务，并监督其个人信息处理活动达到法律规定的个人信息保护标准；</span></p>
    <p style="margin-left: 0pt;"><span style="font-size: 10.5pt;">·&nbsp;法律、行政法规或者国家网信部门规定的其他条件。</span></p>
